        price: 33.001
      },
      {
        name: "Algorand",
        price: 2.2165
      }
    ],
    showDropdown: false,
    showProfileDropdown: false,
    showMobileList: false,
    clientComponentIndex: 0,
  }),
  methods: {
    handleShowDropdown() {
      this.showDropdown = !this.showDropdown;
    },
    documentClick(event) {
      if (!event.target.closest(".rate-dropdown-toggle")) {
        this.showDropdown = false;
      }
      if (!event.target.closest(".user-thumb")) {
        this.showProfileDropdown = false;
      }
      if (!event.target.closest(".navbar-nav-outer") && !event.target.closest(".navbar-toggle")) {
        this.showMobileList = false;
      }
    },
    handleShowProfile() {
      this.showProfileDropdown = !this.showProfileDropdown;
    },
    showMobileMenu() {
      this.showMobileList = !this.showMobileList;
    },
    redirect(index) {
      this.clientComponentIndex = index;
      this.showMobileList = false;
    }
  },
  computed: {},
  created() {
     document.addEventListener('click', this.documentClick)
  },
  destroyed () {
    document.removeEventListener('click', this.documentClick)
  },
  mounted() {}
};
</script>
