}
.nav-tabs li:hover::after,
.nav-tabs li.active::after {
  left: 0;
  right: auto;
  width: 100%;
}
.nav-tabs li.active {
  color: #1F2937;
}
.nav-tabs li::after {
  content: '';
  position: absolute;
  right: 0;
  bottom: 0;
  width: 0;
  height: 2px;
  background: #FCD535;
  pointer-events: none;
  transition: .22s;
}
.input-switch {
