  display: flex;
  align-items: center;
  position: relative;
  color: #707A8A;
  cursor: pointer;
}
.input-switch ~ .input-switch-label:not(.error):before {
  height: 24px;
  width: 50px;
  border-radius: 12px;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  position: absolute;
  content: '';
  transition: all .4s;
  background: #d3e0f3;
}
.input-switch:checked ~ .input-switch-label:before {
  background: #FCD535;
}
